  import { BigNumber, ethers } from "ethers";
  import { popup,toast,confirm,heroAttributeDialog,heroSellDialog,heroOfferDialog,heroDebtDialog,heroNameDialog,heroDestroyDialog,tokenApproveDialog} from '@/libraries/dialog.js'
  import { mapState, mapGetters, mapMutations, mapActions } from '@/core/store.js'
  import eventBus from '@/libraries/eventBus.js';
  import { getCurrentLang } from '@/core/localer.js';
  import axios from "axios";

  eventBus.on("onLanguage",function(lang){
    ability.update();
