  const { connectWallet } = mapActions('wallet');
  const { setConfirms } = mapMutations('wallet');
  const { isUserConnected,getBalanceEth } = mapGetters('wallet');
  const { goldAllowance } = mapState('hero');
  const { saveHeroElement,resetHeroElement,saveHeroAttribute,resetHeroAttribute,getHeroInfo,getGoldAllowance,approveGoldAmount,destroyHero,changeHeroName,acceptHeroOffer,cancelHeroOffer,offerHero,revokeSellHero,buyHero,sellHero,redeemHero,pledgeHero} = mapActions('hero');
  const { apiHost,heroHost,analysis } = mapState('game');

  const profile = reactive({
