    getHero: async()=>{
      let block = await getHeroInfo({heroId:route.params.id});
      let hero = block.hero;
      let element = block.element;
      let attribute = block.attribute;

      profile.data.hero.attribute = hero.attribute;
      profile.data.hero.birthday = hero.birthday;
      profile.data.hero.breeding = hero.breeding;
      profile.data.hero.children = hero.children;
      profile.data.hero.double = (hero.double / 100.0).toFixed(2);
      profile.data.hero.element = hero.element;
      profile.data.hero.equipment = hero.equipment;
      profile.data.hero.fatherId = hero.fatherId;
      profile.data.hero.gender = hero.gender;
      profile.data.hero.genecode = hero.genecode;
      profile.data.hero.generation = hero.generation;
      profile.data.hero.level = hero.level;
      profile.data.hero.motherId = hero.motherId;
      profile.data.hero.name = hero.name;
      profile.data.hero.price = ethers.utils.formatEther(hero.price);
      profile.data.hero.race = hero.race;
      profile.data.hero.rarity = hero.rarity;
      profile.data.hero.role = hero.role;
      profile.data.hero.score = hero.score;

      profile.data.element.earth = element.earth ;
      profile.data.element.electric =element.electric ;
      profile.data.element.fire = element.fire;
      profile.data.element.ice =element.ice ;
      profile.data.element.metal =element.metal ;
      profile.data.element.poison =element.poison ;

      profile.data.attribute.agility = attribute.agility;
      profile.data.attribute.intellect =attribute.intellect ;
      profile.data.attribute.luck =attribute.luck ;
      profile.data.attribute.spirit =attribute.spirit ;
      profile.data.attribute.stamina =attribute.stamina ;
      profile.data.attribute.strength =attribute.strength ;

      ability.update();
      profile.calculateElement(profile.data,profile.player);
      profile.calculateAttribute(profile.data,profile.player);
    },
    calculateAttribute: (data,player) =>{

      let maxLevel = 100;
      let attributeRarityPoint = 45;//24（基础点数） + 21(最大稀有度赠送点数)

      player.hp = (data.attribute.stamina * 70).toFixed();
      player.mp = (data.attribute.intellect * 90).toFixed();
      player.hpRecovery = (data.attribute.spirit * 5).toFixed();
      player.mpRecovery = (data.attribute.spirit * 8).toFixed();
      player.lucky = data.attribute.luck>0? ((0.1+data.attribute.luck / (maxLevel * 13 + attributeRarityPoint) * 0.5) * 100).toFixed(2):0;

      player.physicalAttack = (data.attribute.strength * 5).toFixed();
      player.physicalCriticalRate = data.attribute.strength>0? ((0.1+data.attribute.strength / (maxLevel * 13 + attributeRarityPoint) * 0.35) * 100).toFixed(2):0;
      player.physicalAccuracy = (data.attribute.strength * 3).toFixed();

      player.physicalDefense = (data.attribute.stamina * 4).toFixed();
      player.physicalEvasion = (data.attribute.agility * 2).toFixed();
      player.physicalAttackSpeed = data.attribute.agility>0? ((0.01+data.attribute.agility * 2 /(maxLevel * 13 + attributeRarityPoint) * 0.2) * 100).toFixed(2):0;

      player.magicAttack = (data.attribute.intellect * 5).toFixed();
      player.magicCriticalRate = data.attribute.intellect>0? ((0.1+data.attribute.intellect / (maxLevel * 13 + attributeRarityPoint) * 0.35) * 100).toFixed(2):0;
      player.magicAccuracy = (data.attribute.intellect * 3).toFixed();

      player.magicDefense = (data.attribute.spirit * 4).toFixed();
      player.magicEvasion = (data.attribute.agility * 2).toFixed();
      player.magicAttackSpeed = data.attribute.agility>0? ((0.01+data.attribute.agility * 2 /(maxLevel * 13 + attributeRarityPoint) * 0.2) * 100).toFixed(2):0;

      
      //当前元素属性点/(当前开放最大等级*7 + 42)
      //50%*当前幸运点/(当前开放最大等级*7 + 36)
      //二级属性=二级属性成长倍数*一级属性点*log(0.25*(x-1)+1)  + 1
      
    },
    calculateElement: (data,player) =>{

      let maxLevel = 100;
      let elementRarityPoint = 31;//9（基础点数）+22（最大稀有对赠送点数）

      player.elementFireAttack = data.element.fire>0? ((0.1+data.element.fire / (maxLevel*13+elementRarityPoint) * 0.4) * 100).toFixed(2):0;
      player.elementIceAttack = data.element.ice>0?((0.1+data.element.ice / (maxLevel*13+elementRarityPoint) * 0.4) * 100 ).toFixed(2):0;
      player.elementEarthAttack =data.element.earth>0? ((0.1+data.element.earth / (maxLevel*13+elementRarityPoint) * 0.4) * 100 ).toFixed(2):0;
      player.elementPoisonAttack =data.element.poison>0? ((0.1+data.element.poison / (maxLevel*13+elementRarityPoint) * 0.4) * 100 ).toFixed(2):0;
      player.elementElectricAttack =data.element.electric>0? ((0.1+data.element.electric / (maxLevel*13+elementRarityPoint) * 0.4)  * 100).toFixed(2):0;
      player.elementMetalAttack =data.element.metal>0? ((0.1+data.element.metal / (maxLevel*13+elementRarityPoint)  * 0.4) * 100).toFixed(2):0;
      
    },
    getProfile: ()=>{
      profile.loading = true;

      let id = route.params.id;
      let url = apiHost.value+"/hero/"+(id?id:0);
        axios.get(url)
        .then(r => {
          if(r.data && r.data.data && r.data.data.hero.id>0){
            profile.data = r.data.data;
            ability.update();
            profile.calculateElement(profile.data,profile.player);
            profile.calculateAttribute(profile.data,profile.player);
            profile.getHero();
          }
        })
        .catch(e => {
          console.log(e);
        })
        .finally(() => {
          profile.loading = false;
        });
    },
    getRarityColor: (rarity)=>{
        if(rarity == 1){
            return "bg-gray-600";
        }else if(rarity == 2){
            return "bg-green-600";
        }else if(rarity == 3){
            return "bg-blue-600";
        }else if(rarity == 4){
            return "bg-purple-600";
        }else if(rarity == 5){
            return "bg-orange-600";
        }else if(rarity == 6){
            return "bg-red-600";
        }else{
          return "bg-gray-600";
        }
