                {name: "STR "+profile.data.attribute.strength, max: max},
                {name: "AGI "+profile.data.attribute.agility, max: max},
                {name: "SPI "+profile.data.attribute.spirit, max: max},
                {name: "LUK "+profile.data.attribute.luck, max: max},
            ]
