  const controller = reactive({
    isDialog:false,
    checkApproved: async()=>{
      let allowance = await getGoldAllowance();
      let isNotApproved = allowance < 50000000;
      if(isNotApproved){
        await tokenApproveDialog({approveGoldAmount},language.value,controller);
        return false;
      }else{
        return true;
      }
    },
    buyNow: async() =>{
      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      if(profile.data.hero.id<=0){
        popup('info',language.value.pageIsLoading);
        return;
      }

      if(profile.data.hero.marketPrice<=0){
        popup('error',language.value.heroes.dialog.sell.errorAmount);
        return;
      }

      let allowance = await controller.checkApproved();
      if(allowance && !controller.isDialog){
        await buyHero({heroId:profile.data.hero.id,price:profile.data.hero.marketPrice});
      }

      
    },
    showSell: async() =>{
      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      if(profile.data.hero.id<=0){
        popup('info',language.value.pageIsLoading);
        return;
      }

      let allowance = await controller.checkApproved();
      if(allowance && !controller.isDialog){
        await heroSellDialog({sellHero,revokeSellHero},profile.data,language.value,analysis.value,controller);
      }

      
    },
    acceptOffer: async(buyer,price) =>{
      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      if(profile.data.hero.id<=0){
        popup('info',language.value.pageIsLoading);
        return;
      }

      let allowance = await controller.checkApproved();
      if(allowance && !controller.isDialog){
        await acceptHeroOffer({buyer:buyer,heroId:profile.data.hero.id,price:price });
      }
      
      
    },
    revokeOffer: async(buyer,price) =>{

      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      if(profile.data.hero.id<=0){
        popup('info',language.value.pageIsLoading);
        return;
      }

      let allowance = await controller.checkApproved();
      if(allowance && !controller.isDialog){
        await cancelHeroOffer({heroId:profile.data.hero.id});
      }
      
    },
    showOffer: async() =>{
      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      if(profile.data.hero.id<=0){
        popup('info',language.value.pageIsLoading);
        return;
      }

      let allowance = await controller.checkApproved();
      if(allowance && !controller.isDialog){
        await heroOfferDialog({offerHero,cancelHeroOffer},profile.data,language.value,analysis.value,controller,account.value);
      }
      
    },
    showDebt: async() =>{
      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      if(profile.data.hero.id<=0){
        popup('info',language.value.pageIsLoading);
        return;
      }
      
      let allowance = await controller.checkApproved();
      if(allowance && !controller.isDialog){
        await heroDebtDialog({redeemHero,pledgeHero},profile.data,language.value,analysis.value,controller);
      }

    },
    showAttribute: async ()=>{

      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      if(analysis.value.resetAttributeFee<=0 || profile.data.hero.id<=0){
        popup('info',language.value.pageIsLoading);
        return;
      }

      let allowance = await controller.checkApproved();
      if(allowance && !controller.isDialog){
        await heroAttributeDialog({saveHeroElement,saveHeroAttribute, resetHeroElement,resetHeroAttribute,getGoldAllowance},profile,language.value,analysis.value,controller);
      }

    },
    changeName: async()=>{

      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      if(profile.data.hero.id<=0){
        popup('info',language.value.pageIsLoading);
        return;
