      let allowance = await controller.checkApproved();
      if(allowance && !controller.isDialog){
        await heroNameDialog({changeHeroName,getGoldAllowance},profile.data,language.value,analysis.value,controller);
      }

    },
    destroyHero: async()=>{

      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      if(profile.data.hero.id<=0){
        popup('info',language.value.pageIsLoading);
        return;
      }

      if(!controller.isDialog){
        await heroDestroyDialog({destroyHero},profile.data,language.value,analysis.value,controller);
      }

    },
  });


