<script setup>
  import { onMounted, onUnmounted,reactive, defineProps,ref, toRefs,axios } from 'vue';
  import { useLocaler, useLocale } from 'vue-localer';
  import { useState, useActions, useComputeds } from '@/core/store.js';
  import VideoPlayer from '@/components/element/VideoPlayer.vue'
  import Loading from '@/components/element/Loading.vue';
  import { mapState, mapGetters, mapMutations, mapActions } from '@/core/store.js'
  
  const { apiHost,heroHost,analysis } = mapState('game');
  const props = defineProps(['id','item','image','language']);
  const character = reactive({
    status:0,
    width:800,
    height:800,
    degree:24,
    fullwidth:12000,
    loading:{},
    processes:{},
    thumbnail:{},
    movies:{},
    getUrl:id =>{
      return heroHost.value+id+"/"+id+"_market_fullview.png";
    },
    getElement:id =>{
      return document.querySelector("#model-"+id);
    },
    onResize : (event) => {
      let models = document.querySelectorAll(".model-image");
      for( let i=0; i< models.length; i++ )
      {
        let model = models[i];
        character.rotateImage(model,0);
      }
    },
    onMouseEnterThumbnail : (id) => {
      character.loadImage(id);
    },
    onMouseLeaveThumbnail : (id) =>{
      
      if(character.processes[id]==true)
      {
        character.rotateImage(character.getElement(id),0);
      }else{
        character.thumbnail[id]=false;
      }
    },
    onMouseMoveThumbnail : (id) =>{
      if(character.processes[id]==true)
      {
        character.thumbnail[id]=true;
      }
    },
    onMouseMoveAtlas : (event) =>{
      let degree = event.offsetX / event.target.offsetWidth * 360;
      character.rotateImage(event.target,degree);
    },
    onMouseEnterAtlas : (id) => {

    },
    onMouseLeaveAtlas: (id) =>{
      character.thumbnail[id]=false;
    },
    start: ()=> {
      window.addEventListener('resize', character.onResize);
    },
    stop: ()=> {
      window.removeEventListener('resize', character.onResize);
    },
    loadImage : (id) => {

      
    if(character.processes[id]==true)
    {
      character.thumbnail[id]=true;
      return;
    }

    if(character.loading[id]==true)
    {
      return;
    }
    character.loading[id] = true;

    let url = character.getUrl(id);
    let preview = character.getElement(id);
    let loading = character.getElement("loading-"+id);
    let xmlHTTP = new XMLHttpRequest();
    xmlHTTP.open('GET', url,true);
    xmlHTTP.responseType = 'arraybuffer';
    xmlHTTP.onloadend = function(e) {
      let blob = new Blob([this.response]);
      let url = window.URL.createObjectURL(blob);
      preview.style.backgroundImage = "url('"+url+"')";
      character.rotateImage(preview,0);
      character.processes[id]=true;
      character.movies={};
      character.movies[id]=true;
      character.loading[id] = false;
    };
    xmlHTTP.onprogress = function(e) {
      let percentage = Math.floor((e.loaded / e.total) * 100);
      loading.style.width = percentage+"%";
    };
    xmlHTTP.onloadstart = function() {};
    xmlHTTP.onerror = function () {character.loading[id] = false;};
    xmlHTTP.send();
  },
  rotateImage : (target, degree) => {
    let heighRatio = target.offsetHeight / character.height;
    let widthRatio = target.offsetWidth / character.width;
    let offset = Math.floor(degree / character.degree);
    target.style.backgroundPosition = -offset * character.width * widthRatio + "px center";
    target.style.backgroundSize = character.fullwidth * widthRatio + "px "+ character.height * heighRatio + "px";
  },
});

onMounted(() => {
  character.start();
});

onUnmounted(()=>{
  character.stop();
});
</script>

<template>
  <div class="relative max-h-30rem max-w-30rem mx-auto" @mouseenter="character.onMouseEnterThumbnail(props.id)" @mouseleave="character.onMouseLeaveThumbnail(props.id)" @mousemove="character.onMouseMoveThumbnail(props.id)">
    <div class="w-full h-full">
      <div :id="'model-loading-'+props.id" class="bg-primary w-0 h-0.1rem my-0.2rem" v-show="!character.processes[props.id] && character.loading[props.id]"></div>
      <div :style="'background:url(\''+(!character.thumbnail[props.id] && (props.item.status==-1 || props.item.status==3)?heroHost+props.id+'/'+props.id+'_market_thumbnail.png':'')+'\') center no-repeat;background-size:contain;'">
        <VideoPlayer class="absolute top-0 left-0 w-full h-full animate__animated animate__fadeIn" maxnone="true" v-if="!character.thumbnail[props.id] && (props.item.status==-1 || props.item.status==3)" :poster="heroHost+props.id+'/'+props.id+'_market_thumbnail.png'" :src="heroHost+props.id+'/'+props.id+'_market.mp4'"></VideoPlayer>
        <img class="h-30rem w-auto" src="/images/alpha_preview.png">
      </div>
    </div>
    <div class=" absolute top-0 left-0 bottom-0 right-0 overflow-hidden" v-show="character.processes[props.id]==true && character.thumbnail[props.id]==true && (props.item.status==-1 || props.item.status==3)">
      <div class="model-image max-h-30rem max-w-30rem animate__animated animate__fadeIn" :id="'model-'+props.id" @mousemove="character.onMouseMoveAtlas($event)" @mouseenter="character.onMouseEnterAtlas(props.id)"  @mouseleave="character.onMouseLeaveAtlas(props.id)"></div>
    </div>
    <div class="absolute top-1/4 w-full z-0 flex flex-col justify-center items-center" v-show="props.item.status>-1 && props.item.status<3" >
        <h1 class="text-sm my-1rem">{{language.heroes.rendering.heroRendering}}</h1>
        <Loading theme="1"></Loading>
        <h1 class="text-sm my-1rem" >{{language.heroes.rendering.heroQueue.replace("{num}",""+props.item.queue+"")}}</h1>
    </div>
  </div>
</template>

<style scoped>
.model-image {
  position: absolute;
  width: 100%;
  height: calc(100%);
  background-size: '-1000px -1000px';
}
</style>